import React from "react"

import {NotFoundBlock} from "../Components"

type NotFoundProps = {
  description?: string;
  title?: string;
}
const NotFound: React.FC<NotFoundProps> = ({
  description = "    ",
  title = "  "
}) =>  {
  return (
    <div className="container">
      <NotFoundBlock description = {description} title ={title}/>
    </div>
    
  )
}
export default NotFound